import axios from 'axios';
import { useEffect, useState } from 'react';
import {connect} from 'react-redux';
import { useHistory, Link } from 'react-router-dom';
import {FontAwesomeIcon} from '@fortawesome/react-fontawesome';
import {faColumns, faFistRaised, faFlushed, faShieldAlt, faTshirt} from '@fortawesome/free-solid-svg-icons';

import saharPic from '../imgs/herculeProfile.webp';
import edenPic from '../imgs/gohanProfile.webp';

let updateInterval;

function Board(props) {
    const [serverFull, setServerFull] = useState(false);
    const [win, setWin] = useState(false);
    const [phase, setPhase] = useState(1);
    const [selection, setSelection] = useState();
    const [selCharacter, setSelCharacter] = useState();
    const [enemySelCharacter, setEnemySelCharacter] = useState();
    const [enemySelection, setEnemySelection] = useState();
    const [gameReady, setGameReady] = useState();
    const [hp, setHp] = useState(150);
    const [stamina, setStamina] = useState(100);
    const [enemyHp, setEnemyHp] = useState(150);
    const [enemyStamina, setEnemyStamina] = useState(100);
    const [fightString, setFightString] = useState();

    const history = useHistory();

    const makeMove = payload => {
        axios.post('https://dbzgame.herokuapp.com/play', {server: props.server, username: props.username, phase: phase, payload})
        .then(response => response.data)
        .then(json => {
            if(json.bool) {
                if(json.switchPhase == 2) {
                    if(props.username == json.game.host) {
                        setHp(json.game.hostStats.hp);
                        setStamina(json.game.hostStats.stamina);
                        setEnemyHp(json.game.secondStats.hp);
                        setEnemyStamina(json.game.secondStats.stamina);
                    } else {
                        setHp(json.game.secondStats.hp);
                        setStamina(json.game.secondStats.stamina);
                        setEnemyHp(json.game.hostStats.hp);
                        setEnemyStamina(json.game.hostStats.stamina);
                    }
                    if(json.game.win) {
                        setWin(json.game.win);
                    }
                    let tempFightString = json.game.fightString.split('\n');
                    tempFightString = tempFightString.map((val, index) => {
                        val = val.replace(`${json.game.host == props.username ? json.game.hostSelectedChar : json.game.secondSelectedChar} (${props.username}) was`, 'You were');
                        val = val.replace(`${json.game.host == props.username ? json.game.hostSelectedChar : json.game.secondSelectedChar} (${props.username})`, 'You');
                        val = val.replace('You', 'You');
                        if(val[0] == 'Y' && val[1] == 'o' && val[2] == 'u') {
                            val = val.replace('his', 'your');
                            val = val.replace('her', 'your');
                            val = val.replace('has taken', 'took');
                            val = val.replace('is', 'are');
                            val = val.replace('enters', 'enter');
                            if(index <= 1) {
                                return <div style={{color: 'blue'}}>{val}</div>
                            }
                            if(val.includes('hit')) {
                                return <div style={{color: 'red'}}>{val}</div>
                            } else {
                                return <div style={{color: 'green'}}>{val}</div>
                            }
                        } else {
                            if(index <= 1) {
                                return <div style={{color: 'blue'}}>{val}</div>
                            }
                            if(val.includes('hit')) {
                                return <div style={{color: 'green'}}>{val}</div>
                            } else {
                                return <div style={{color: 'red'}}>{val}</div>
                            }
                        }
                    });
                    let tempSoundsOrder = json.game.soundsOrder.split('\n');
                    tempSoundsOrder.map((val, index) => {
                        if(index == 0) {return val}
                        setTimeout(() => {
                            document.getElementById(val).play();
                        }, 1000*index);
                    });
                    setFightString(tempFightString);
                } else {
                    setFightString('');
                }
                setPhase(json.switchPhase);
            }
        })
        .catch(() => alert('Some error occured!'));
    }

    const selectCharacter = char => {
        axios.post('https://dbzgame.herokuapp.com/selectchar', {server: props.server, username: props.username, char})
        .then(response => response.data)
        .then(json => {
            if(json.bool) {
                setSelection(`${json.char} (${props.username})`);
                setSelCharacter(json.char);
                setPhase(2);
            }
        })
        .catch(() => alert('Some error occured!'));
    }

    const mock = () => {
        axios.post('https://dbzgame.herokuapp.com/mock', {server: props.server, username: props.username, char: selCharacter})
        .then(response => response.data)
        .then(json => {
            if(json.bool) {
                document.getElementById(json.mockSound).play();
            }
        })
        .catch(() => alert('Some error occured!'));
    }

    useEffect(() => {
        if(updateInterval) {
            clearInterval(updateInterval);
            updateInterval = undefined;
        }
        if(!win) {
            updateInterval = setInterval(() => {
                axios.get(`https://dbzgame.herokuapp.com/update/${props.server}`)
                .then(response => response.data)
                .then(json => {
                    setServerFull(json.game.full);
                    if(json.game.hostSelectedChar && json.game.secondSelectedChar) {
                        setGameReady(true);
                        if(json.game.host == props.username) {
                            setEnemySelection(`${json.game.secondSelectedChar} (${json.game.secondPlayer})`);
                            setEnemySelCharacter(json.game.secondSelectedChar);
                        } else {
                            setEnemySelection(`${json.game.hostSelectedChar} (${json.game.hostPlayer})`);
                            setEnemySelCharacter(json.game.hostSelectedChar);
                        }
                    }
                    if(json.game.host == props.username && json.game.secondMock) {
                        axios.post(`https://dbzgame.herokuapp.com/resetmock`, {server: props.server, username: props.username})
                        .then(response2 => response2.data)
                        .then(json2 => {
                            if(json2.bool) {
                                document.getElementById(json.game.secondMock).play();
                            }
                        });
                    } else if(json.game.secondPlayer == props.username && json.game.hostMock) {
                        axios.post(`https://dbzgame.herokuapp.com/resetmock`, {server: props.server, username: props.username})
                        .then(response2 => response2.data)
                        .then(json2 => {
                            if(json2.bool) {
                                document.getElementById(json.game.hostMock).play();
                            }
                        });
                    }
                    if(json.game.advance && phase == 5) {
                        makeMove('fight');
                    }
                })
                .catch(() => {
                    clearInterval(updateInterval);
                    updateInterval = undefined;
                    history.push('/');
                    alert('Some error occured!');
                });
            }, 1000);
        }
    });
    return (
        <div className="text-center">
            {serverFull && gameReady ? <p>Game on!</p> : serverFull ? <p>Waiting for character selection..</p> : <p>{`Waiting for the other player... (server: ${props.username})`}</p>}
            <hr/>
            {selection && !gameReady ? <div style={{left: 10}}>
                <div>You as: <strong>{selection}</strong></div>
                <br/>
                <img src={selCharacter == 'Gohan' ? edenPic : saharPic} style={{width: '170px', borderRadius: '10px 10px 0 0'}}/>
                <div class="progress rounded-0">
                    <div class="progress-bar bg-danger" role="progressbar" style={{width: `${hp/150*100}%`}} aria-valuenow="25" aria-valuemin="0" aria-valuemax="100">{`${(hp/150*100).toFixed(0)}%`}</div>
                </div>
                <div class="progress" style={{borderRadius: '0 0 0 0'}}>
                    <div class="progress-bar bg-warning" role="progressbar" style={{width: `${stamina}%`}} aria-valuenow="25" aria-valuemin="0" aria-valuemax="100">{`${stamina}%`}</div>
                </div>
            </div> : ''}
            {gameReady ? <div className="d-flex justify-content-between" style={{right: 10}}>
                <div>
                    <div>You as: <strong>{selection}</strong></div>
                    <br/>
                    <img src={selCharacter == 'Gohan' ? edenPic : saharPic} style={{borderRadius: '0 10px 0 0'}}/>
                    <div class="progress rounded-0">
                        <div class="progress-bar bg-danger" role="progressbar" style={{width: `${hp/150*100}%`}} aria-valuenow="25" aria-valuemin="0" aria-valuemax="100">{`${(hp/150*100).toFixed(0)}%`}</div>
                    </div>
                    <div class="progress" style={{borderRadius: '0 0 10px 0'}}>
                        <div class="progress-bar bg-warning" role="progressbar" style={{width: `${stamina}%`}} aria-valuenow="25" aria-valuemin="0" aria-valuemax="100">{`${stamina}%`}</div>
                    </div>
                </div>
                <div style={{width: '20%'}}></div>
                <div>
                    <div>Enemy as: <strong>{enemySelection}</strong></div>
                    <br/>
                    <img src={enemySelCharacter == 'Gohan' ? edenPic : saharPic} style={{borderRadius: '10px 0 0 0'}}/>
                    <div class="progress rounded-0" dir="rtl">
                        <div class="progress-bar bg-danger" role="progressbar" style={{width: `${enemyHp/150*100}%`}} aria-valuenow="25" aria-valuemin="0" aria-valuemax="100">{`${(enemyHp/150*100).toFixed(0)}%`}</div>
                    </div>
                    <div class="progress" style={{borderRadius: '0 0 0 10px'}} dir="rtl">
                        <div class="progress-bar bg-warning" role="progressbar" style={{width: `${enemyStamina}%`}} aria-valuenow="25" aria-valuemin="0" aria-valuemax="100">{`${enemyStamina}%`}</div>
                    </div>
                </div>

            </div> : ''}
            {gameReady && !win ?
            <div>
                <button className="btn btn-info mt-1" onClick={mock}>Mock</button>
            </div>
            :
            ''}
            <div>
                {!win ? phase == 1 ?
                <div>
                    <div style={{backgroundImage: `url(${saharPic})`, backgroundSize: 'contain', backgroundRepeat: 'no-repeat', backgroundPosition: 'center'}} className="btn mx-3 p-5" onClick={() => [document.getElementById('menu-sound').play(), selectCharacter('Hercule')]}></div>
                    <div style={{backgroundImage: `url(${edenPic})`, backgroundSize: 'contain', backgroundRepeat: 'no-repeat', backgroundPosition: 'center'}} className="btn mx-3 p-5" onClick={() => [document.getElementById('menu-sound').play(), selectCharacter('Gohan')]}></div>
                </div>
                :
                phase == 2 && gameReady ?
                <div>
                    <p>Choose a stance!</p>
                    <button className="btn btn-primary" style={{borderRadius: '10px 0 0 10px', width: '120px'}} onClick={e => makeMove('Defensive Stance')}><FontAwesomeIcon icon={faShieldAlt} size="5x"/></button>
                    <button className="btn btn-danger" style={{borderRadius: '0 10px 10px 0', width: '120px'}} onClick={e => makeMove('Offensive Stance')}><FontAwesomeIcon icon={faFistRaised} size="5x"/></button>
                </div>
                :
                phase == 3 && gameReady ?
                <div>
                    <p>Choose where to defend!</p>
                    <button className="btn btn-primary" style={{borderRadius: '10px 10px 0 0', width: '130px'}} onClick={() => makeMove('Head')}><FontAwesomeIcon icon={faFlushed} size="5x"/></button>
                    <br/>
                    <button className="btn btn-primary" style={{borderRadius: '0 0 0 0', width: '130px'}} onClick={() => makeMove('Torso')}><FontAwesomeIcon icon={faTshirt} size="5x"/></button>
                    <br/>
                    <button className="btn btn-primary" style={{borderRadius: '0 0 10px 10px', width: '130px'}} onClick={() => makeMove('Legs')}><FontAwesomeIcon icon={faColumns} size="5x"/></button>
                </div>
                :
                phase == 4 && gameReady ?
                <div>
                    <div>
                    <p>Choose where to attack!</p>
                    <button className="btn btn-danger" style={{borderRadius: '10px 10px 0 0', width: '130px'}} onClick={() => makeMove('Head')}><FontAwesomeIcon icon={faFlushed} size="5x"/></button>
                    <br/>
                    <button className="btn btn-danger" style={{borderRadius: '0 0 0 0', width: '130px'}} onClick={() => makeMove('Torso')}><FontAwesomeIcon icon={faTshirt} size="5x"/></button>
                    <br/>
                    <button className="btn btn-danger" style={{borderRadius: '0 0 10px 10px', width: '130px'}} onClick={() => makeMove('Legs')}><FontAwesomeIcon icon={faColumns} size="5x"/></button>
                </div>
                </div>
                :
                phase == 5 && gameReady ?
                <div>
                    <p>Waiting for other player...</p>
                </div>
                :
                ''
                :
                ''}
            </div>
            {win ? <div><p style={{whiteSpace: 'pre-wrap'}}>{fightString}</p><Link className="btn btn-primary" to="/">Go back</Link></div> : serverFull && gameReady ? <p style={{whiteSpace: 'pre-wrap'}}>{fightString}</p> : ''}
        </div>
    )
}

const mapStateToProps = state => {
    return {
        server: state.online.server,
        username: state.online.username
    }
}

const mapDispatchToProps = dispatch => {
    return {
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(Board);