import axios from 'axios';
import { useState } from 'react';
import {connect} from 'react-redux';

import {setOnline, setUsername} from '../redux/';

import loadingWebp from '../imgs/loading.webp';

function Connect(props) {
    const [loading, setLoading] = useState(false);
    const [inputUsername, setInputUsername] = useState();

    const connectToServer = () => {
        setLoading(true);
        axios.get(`https://dbzgame.herokuapp.com/connect/${inputUsername}`)
        .then(response => response.data)
        .then(json => {
            if(json.bool) {
                props.setUsername(json.username);
                props.setOnline(true);
            } else {
                alert('User already connected!');
                document.getElementById('menu-sound-off').play();
                setLoading(false);
            }
        })
        .catch(() => [setLoading(false), alert('Some error occured!')]);
    }
    
    return (
        <div>
            {loading ? 
            <div>
                <img src={loadingWebp}/>
            </div>
            :
            <div className="mt-5">
                <p className="display-4" style={{textShadow: '2px 2px 10px blue'}}>DBZ Game</p>
                <input placeholder="Enter username" className="text-center my-3" onChange={e => setInputUsername(e.target.value)}/>
                <br/>
                <button className="btn btn-danger" onClick={() => [document.getElementById('menu-sound').play(), inputUsername ? connectToServer() : [alert('Enter a username!'), document.getElementById('menu-sound-off').play()]]}>Connect to server</button>
            </div>}
        </div>
    )
}

const mapStateToProps = state => {
    return {
        isOnline: state.online.isOnline
    }
}

const mapDispatchToProps = dispatch => {
    return {
        setOnline: val => dispatch(setOnline(val)),
        setUsername: val => dispatch(setUsername(val))
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(Connect);